import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      component: () => import('@/components/Layout.vue'),
      children: [
        { path: '', component: () => import('@/components/page/Myself.vue') },
        { path: '/look', component: () => import('@/components/page/Look.vue') },
        { path: '/message', component: () => import('@/components/page/Message.vue') }
      ]
    }
  ]
})



